.App{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: rgb(112, 153, 230);
    transition: 0.3s;
}
.container-quotes{
    padding: 30px;
    min-height: 200px;
    width: 600px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: white;
    transition: 0.3s;
}
.button-quotes-conteiner{
    padding-top: 20px;
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.button-tw-tu{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
}
.t-button{
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: white;
    width: 35px;
    height: 35px;
}
.t-button:hover{
    color: white;
    filter: brightness(1.1);
}
.next-button{
    color: white;
    height: 35px;
    border: none;
}
.next-button:hover{
    filter: brightness(1.1);
}